.rc-rate {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 18px;
  display: inline-block;
  vertical-align: middle;
  font-weight: normal;
  font-style: normal;
  outline: none;
}
.rc-rate-disabled .rc-rate-star:before,
.rc-rate-disabled .rc-rate-star-content:before {
  cursor: default;
}
.rc-rate-star {
  margin: 0;
  padding: 0;
  display: inline-block;
  position: relative;
  transition: all 0.3s;
  color: #e9e9e9;
  cursor: pointer;
  line-height: 1.5;
}
.rc-rate-star-first,
.rc-rate-star-second {
  transition: all 0.3s;
}
.rc-rate-star-first {
  position: absolute;
  top: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
}
.rc-rate-star-half .rc-rate-star-first,
.rc-rate-star-half .rc-rate-star-second {
  opacity: 1;
}
.rc-rate-star-half .rc-rate-star-first,
.rc-rate-star-full .rc-rate-star-second {
  color: #f5a623;
}
.rc-rate-star-half:hover .rc-rate-star-first,
.rc-rate-star-full:hover .rc-rate-star-second {
  color: #f8c165;
}
@font-face {
  font-family: "anticon";
  src: url("//at.alicdn.com/t/font_r5u29ls31bgldi.eot");
  /* IE9*/
  src: url("//at.alicdn.com/t/font_r5u29ls31bgldi.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */ url("//at.alicdn.com/t/font_r5u29ls31bgldi.woff") format("woff"), /* chrome、firefox */ url("//at.alicdn.com/t/font_r5u29ls31bgldi.ttf") format("truetype"), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/ url("//at.alicdn.com/t/font_r5u29ls31bgldi.svg#iconfont") format("svg");
  /* iOS 4.1- */
}
.anticon {
  font-style: normal;
  vertical-align: baseline;
  text-align: center;
  text-transform: none;
  line-height: 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.anticon:before {
  display: block;
  font-family: "anticon" !important;
}
.anticon-star:before {
  content: "\e660";
}
.rc-rate {
  direction: ltr;
}
.a-ltr.rc-rate-disabled .a-ltr.rc-rate-star:hover {
  transform: scale(1);
}
.a-ltr.rc-rate-star {
  margin-right: 8px;
}
.a-ltr.rc-rate-star-focused,
.a-ltr.rc-rate-star:hover {
  transform: scale(1.1);
}
.a-ltr.rc-rate-star-first {
  left: 0;
}
.a-rtl.rc-rate {
  transform: scale(-1, 1);
}
.a-rtl.rc-rate-disabled .a-rtl.rc-rate-star:hover {
  transform: scale(-1);
}
.a-rtl.rc-rate-star {
  margin-left: 8px;
}
.a-rtl.rc-rate-star-focused,
.a-rtl.rc-rate-star:hover {
  transform: scale(-1.1);
}
.a-rtl.rc-rate-star-first {
  right: 0;
}
